/* eslint-disable camelcase */
import React from 'react';
import { Card, Badge, Row } from 'reactstrap';
import classnames from 'classnames';
import { ContextMenuTrigger } from 'react-contextmenu';

import { Colxx } from '../../../../components/common/CustomBootstrap';

import Pagination from './pagination';
import ContextMenuContainer from './context-menu-container';

const getStatusBadge = (isSuperAdmin) => {
  return isSuperAdmin ? (
    <Badge color="primary" pill>
      Super Admin
    </Badge>
  ) : (
    <Badge color="secondary" pill>
      Admin
    </Badge>
  );
};

const getEnabledStatus = (isEnabled) => {
  return (
    <Badge color="text" pill>
      {isEnabled ? (
        <i className="mb-0 h5 simple-icon-check text-success" />
      ) : (
        <i className="mb-0 h5 simple-icon-close text-danger" />
      )}
    </Badge>
  );
};

const TableItem = ({ item, isSelect, collect, onCheckItem }) => {
  const { email, name, phone_number, is_superadmin, is_enabled } = item;

  return (
    <Colxx xxs="12" className="mb-3">
      <ContextMenuTrigger id="menu_id" data={item} collect={collect}>
        <Card
          onClick={(event) => onCheckItem(event, item.email)}
          className={classnames('d-flex flex-row', {
            active: isSelect,
          })}
        >
          <div className="pl-2 d-flex flex-grow-1 min-width-zero">
            <div className="card-body align-self-center d-flex flex-column flex-lg-row justify-content-between min-width-zero align-items-lg-center">
              <p className="list-item-heading mb-1 truncate w-30 w-sm-100">
                {name}
              </p>
              <p className="mb-0 text-muted text-center w-20 w-sm-100">
                {email}
              </p>
              <p className="mb-0 text-muted text-center w-20 w-sm-100">
                {phone_number}
              </p>
              <p className="mb-0 text-center w-20 w-sm-100">
                {getStatusBadge(is_superadmin)}
              </p>
              <p className="mb-0 text-muted text-center w-10 w-sm-100">
                {getEnabledStatus(is_enabled)}
              </p>
            </div>
          </div>
        </Card>
      </ContextMenuTrigger>
    </Colxx>
  );
};

function collectData(props) {
  return { data: props.data };
}

const DataTable = ({
  items,
  selectedItems,
  onCheckItem,
  currentPage,
  totalPage,
  onContextMenuClick,
  onContextMenu,
  onChangePage,
}) => {
  return (
    <Row>
      {items.map((item) => (
        <TableItem
          key={item.email}
          item={item}
          isSelect={selectedItems.includes(item.email)}
          onCheckItem={onCheckItem}
          collect={collectData}
        />
      ))}
      <Pagination
        currentPage={currentPage}
        totalPage={totalPage}
        onChangePage={(i) => onChangePage(i)}
      />
      <ContextMenuContainer
        onContextMenuClick={onContextMenuClick}
        onContextMenu={onContextMenu}
      />
    </Row>
  );
};

export default DataTable;
