const INIT_STATE = () => ({
  name: '',
  email: '',
  phone_number: '',
  password: '',
  is_superadmin: false,
  client_add: false,
  client_edit: false,
  services_add: false,
  services_edit: false,
  category_add: false,
  category_edit: false,
  status_add: false,
  status_edit: false,
  is_enabled: true,
});

const AddNewModal = ({
  modalOpen,
  toggleModal,
  accessToken,
  filters,
  getUsersAction,
}) => {
  const [values, setValues] = useState(INIT_STATE);

  const handleChange = (event) => {
    setValues({ ...values, [event.target.id]: event.target.value });
  };

  const handleToggle = (event) => {
    if (event.target.id === 'is_superadmin') {
      setValues({
        ...values,
        [event.target.id]: !values[event.target.id],
        client_add: !values[event.target.id],
        client_edit: !values[event.target.id],
        services_add: !values[event.target.id],
        services_edit: !values[event.target.id],
        category_add: !values[event.target.id],
        category_edit: !values[event.target.id],
        status_add: !values[event.target.id],
        status_edit: !values[event.target.id],
      });
    } else {
      setValues({ ...values, [event.target.id]: !values[event.target.id] });
    }
  };

  const [doRequest, loading] = useRequest({
    url: '/users/add-user',
    method: 'POST',
    headers: {
      Authorization: accessToken,
    },
    data: values,
    nextSuccess: () => {
      NotificationManager.success(
        'User Added Successfully',
        'User Operation Success',
        3000,
        null,
        null,
        ''
      );
      getUsersAction(accessToken, filters);
      wait(1200, () => {
        setValues(INIT_STATE);
        toggleModal(false);
      });
    },
    nextError: () => {
      NotificationManager.error(
        'Email Already Exist!',
        'User Operation Error',
        3000,
        null,
        null,
        ''
      );
    },
  });

  const handleSubmit = () => {
    if (values.password.length <= 3) {
      NotificationManager.error(
        'Password should be more than 3 characters.',
        'User Operation Error',
        3000,
        null,
        null,
        ''
      );
    } else {
      doRequest();
    }
  };

  return (
    <Modal
      isOpen={modalOpen}
      toggle={() => {
        toggleModal();
        setValues(INIT_STATE);
      }}
      wrapClassName="modal-right"
      backdrop="static"
    >
      <ModalHeader
        toggle={() => {
          toggleModal();
          setValues(INIT_STATE);
        }}
      >
        <IntlMessages id="pages.add-new-modal-title" />
      </ModalHeader>
      <ModalBody>
        <Label>
          <IntlMessages id="pages.user-name" />
        </Label>
        <Input id="name" value={values.name} required onChange={handleChange} />

        <Label className="mt-4">
          <IntlMessages id="pages.user-email" />
        </Label>
        <Input
          id="email"
          required
          pattern="/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i"
          placeholder="example@email.com"
          value={values.email}
          onChange={handleChange}
        />

        <Label className="mt-4">
          <IntlMessages id="pages.user-password" />
        </Label>
        <Input
          id="password"
          value={values.password}
          required
          onChange={handleChange}
        />

        <Label className="mt-4">
          <IntlMessages id="pages.user-phone" />
        </Label>
        <Input
          id="phone_number"
          value={values.phone_number}
          onChange={handleChange}
        />

        <Row className="mt-4">
          <Colxx className="d-flex flex-column justify-content-center">
            <Label className="m-0 align-middle">
              <IntlMessages id="pages.user-is-super-admin" />
            </Label>
          </Colxx>

          <Colxx className="d-flex flex-column justify-content-center">
            <CustomInput
              id="is_superadmin"
              type="checkbox"
              checked={values.is_superadmin}
              onChange={handleToggle}
            />
          </Colxx>
        </Row>

        <Row className="mt-4">
          <Colxx className="d-flex flex-column justify-content-center">
            <Label className="m-0 align-middle">
              <IntlMessages id="pages.user-is-enabled" />
            </Label>
          </Colxx>
          <Colxx className="d-flex flex-column justify-content-center">
            <CustomInput
              id="is_enabled"
              type="checkbox"
              checked={values.is_enabled}
              onChange={handleToggle}
            />
          </Colxx>
        </Row>

        <Row className="mt-4">
          <Colxx xxs="6" className="d-flex flex-column justify-content-center">
            <Label className="m-0 align-middle">
              <IntlMessages id="pages.permission" />
            </Label>
          </Colxx>
          <Colxx xxs="3" className="d-flex flex-column justify-content-center">
            <Label className="m-0 align-middle">
              <IntlMessages id="pages.add" />
            </Label>
          </Colxx>
          <Colxx xxs="3" className="d-flex flex-column justify-content-center">
            <Label className="m-0 align-middle">
              <IntlMessages id="pages.edit" />
            </Label>
          </Colxx>
        </Row>

        <Separator />

        <Row className="mt-2">
          <Colxx xxs="6" className="d-flex flex-column justify-content-center">
            <Label className="m-0 align-middle">
              <IntlMessages id="pages.category" />
            </Label>
          </Colxx>
          <Colxx xxs="3" className="d-flex flex-column justify-content-center">
            <CustomInput
              id="category_add"
              type="checkbox"
              checked={values.category_add}
              onChange={handleToggle}
            />
          </Colxx>
          <Colxx xxs="3" className="d-flex flex-column justify-content-center">
            <CustomInput
              id="category_edit"
              type="checkbox"
              checked={values.category_edit}
              onChange={handleToggle}
            />
          </Colxx>
        </Row>
        <Row className="mt-2">
          <Colxx xxs="6" className="d-flex flex-column justify-content-center">
            <Label className="m-0 align-middle">
              <IntlMessages id="pages.client" />
            </Label>
          </Colxx>
          <Colxx xxs="3" className="d-flex flex-column justify-content-center">
            <CustomInput
              id="client_add"
              type="checkbox"
              checked={values.client_add}
              onChange={handleToggle}
            />
          </Colxx>
          <Colxx xxs="3" className="d-flex flex-column justify-content-center">
            <Label className="m-0 align-middle">
              <CustomInput
                id="client_edit"
                type="checkbox"
                checked={values.client_edit}
                onChange={handleToggle}
              />
            </Label>
          </Colxx>
        </Row>
        <Row className="mt-2">
          <Colxx xxs="6" className="d-flex flex-column justify-content-center">
            <Label className="m-0 align-middle">
              <IntlMessages id="pages.services" />
            </Label>
          </Colxx>
          <Colxx xxs="3" className="d-flex flex-column justify-content-center">
            <CustomInput
              id="services_add"
              type="checkbox"
              checked={values.services_add}
              onChange={handleToggle}
            />
          </Colxx>
          <Colxx xxs="3" className="d-flex flex-column justify-content-center">
            <CustomInput
              id="services_edit"
              type="checkbox"
              checked={values.services_edit}
              onChange={handleToggle}
            />
          </Colxx>
        </Row>
        <Row className="mt-2">
          <Colxx xxs="6" className="d-flex flex-column justify-content-center">
            <Label className="m-0 align-middle">
              <IntlMessages id="pages.status" />
            </Label>
          </Colxx>
          <Colxx xxs="3" className="d-flex flex-column justify-content-center">
            <CustomInput
              id="status_add"
              type="checkbox"
              checked={values.status_add}
              onChange={handleToggle}
            />
          </Colxx>
          <Colxx xxs="3" className="d-flex flex-column justify-content-center">
            <CustomInput
              id="status_edit"
              type="checkbox"
              checked={values.status_edit}
              onChange={handleToggle}
            />
          </Colxx>
        </Row>
      </ModalBody>
      <ModalFooter>
        <Button
          color="danger"
          outline
          onClick={() => {
            toggleModal();
            setValues(INIT_STATE);
          }}
        >
          <IntlMessages id="pages.cancel" />
        </Button>
        <Button
          id="add-new-user"
          color="primary"
          className={`btn-shadow btn-multiple-state ${
            loading ? 'show-spinner' : ''
          }`}
          onClick={() => handleSubmit(values)}
        >
          <span className="spinner d-inline-block">
            <span className="bounce1" />
            <span className="bounce2" />
            <span className="bounce3" />
          </span>
          <span className="label">
            <IntlMessages id="pages.submit" />
          </span>
        </Button>
      </ModalFooter>
    </Modal>
  );
};

const mapStateToProps = ({ authUser, accountsApp }) => {
  const { accessToken } = authUser;
  const { filters } = accountsApp;
  return { accessToken, filters };
};

export default connect(mapStateToProps, {
  getUsersAction: getUsers,
})(AddNewModal);
