import React, { useEffect, useState } from 'react';
import { Row } from 'reactstrap';
import { connect } from 'react-redux';

import { Colxx } from '../../../components/common/CustomBootstrap';

import DataTable from './components/data-table';
import AddNewModal from './components/add-new-modal';
import DataPageHeading from './components/data-page-heading';
import EditModal from './components/edit-modal';
import { getUsers, updateFilters } from '../../../redux/actions';

const orderOptions = [
  { column: 'email', label: 'Email' },
  { column: 'superadmin', label: 'Super Admin' },
  { column: 'admin', label: 'Admin' },
];

const pageSizes = [4, 8, 12, 20];

const getIndex = (value, arr, prop) => {
  for (let i = 0; i < arr.length; i += 1) {
    if (arr[i][prop] === value) {
      return i;
    }
  }
  return -1;
};

const User = ({
  match,
  loading,
  filters,
  allUsersData,
  accessToken,
  getUsersAction,
  updateFiltersAction,
}) => {
  const {
    currentPage,
    selectedOrderOption,
    selectedPageSize,
    search,
  } = filters;

  const { data, totalPage, totalItems } = allUsersData;

  const [addModalOpen, setAddModalOpen] = useState(false);
  const [editModalOpen, setEditModalOpen] = useState(false);
  const [selectedItems, setSelectedItems] = useState([]);
  const [lastChecked, setLastChecked] = useState(null);

  const [selectedItem, setSelectedItem] = useState(() => {
    if (data[0] === undefined)
      return {
        email: '',
        phone_number: '',
        is_superadmin: false,
        client_add: false,
        client_edit: false,
        services_add: false,
        services_edit: false,
        category_add: false,
        category_edit: false,
        status_add: false,
        status_edit: false,
        is_enabled: false,
      };

    return data[0];
  });

  const setSearch = (query) => {
    updateFiltersAction({ ...filters, search: query });
  };

  const setCurrentPage = (newCurrentPage) => {
    updateFiltersAction({ ...filters, currentPage: newCurrentPage });
  };

  const setSelectedPageSize = (newSelectedPageSize) => {
    updateFiltersAction({ ...filters, selectedPageSize: newSelectedPageSize });
  };

  const setSelectedOrderOption = (newSelectedOrderOption) => {
    updateFiltersAction({
      ...filters,
      selectedOrderOption: newSelectedOrderOption,
    });
  };

  useEffect(() => {
    setCurrentPage(1);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedPageSize, selectedOrderOption]);

  useEffect(() => {
    getUsersAction(accessToken, filters);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [selectedPageSize, currentPage, selectedOrderOption, search]);

  const onCheckItem = (event, id) => {
    if (
      event.target.tagName === 'A' ||
      (event.target.parentElement && event.target.parentElement.tagName === 'A')
    ) {
      return true;
    }

    if (lastChecked === null) {
      setLastChecked(id);
    }

    let selectedList = [...selectedItems];
    if (selectedList.includes(id)) {
      selectedList = selectedList.filter((x) => x !== id);
    } else {
      selectedList.push(id);
    }
    setSelectedItems(selectedList);

    if (event.shiftKey) {
      let newItems = [...data];
      const start = getIndex(id, newItems, 'id');
      const end = getIndex(lastChecked, newItems, 'id');
      newItems = newItems.slice(Math.min(start, end), Math.max(start, end) + 1);
      selectedItems.push(
        ...newItems.map((item) => {
          return item.id;
        })
      );
      selectedList = Array.from(new Set(selectedItems));
      setSelectedItems(selectedList);
    }
    document.activeElement.blur();
    return false;
  };

  const onContextMenuClick = (e, item) => {
    switch (item.action) {
      case 'edit':
        setSelectedItem(item.data);
        setEditModalOpen(true);
        break;
      case 'create-new':
        setAddModalOpen(true);
        break;
      default:
        break;
    }
  };

  const onContextMenu = (e, item) => {
    const clickedProductId = item.data.email;
    if (!selectedItems.includes(clickedProductId)) {
      setSelectedItems([clickedProductId]);
    }

    return true;
  };

  const startIndex = (currentPage - 1) * selectedPageSize;
  const endIndex = currentPage * selectedPageSize;

  return (
    <>
      <Row>
        {loading && (
          <div
            className="loading"
            data-keyboard="false"
            data-backdrop="static"
          />
        )}
        <Colxx xxs="12" className="mb-4">
          <div className="disable-text-selection">
            <DataPageHeading
              heading="menu.user"
              changeOrderBy={(column) => {
                setSelectedOrderOption(
                  orderOptions.find((x) => x.column === column)
                );
              }}
              changePageSize={setSelectedPageSize}
              selectedPageSize={selectedPageSize}
              totalItemCount={totalItems}
              selectedOrderOption={selectedOrderOption}
              match={match}
              startIndex={startIndex}
              endIndex={endIndex}
              selectedItemsLength={selectedItems ? selectedItems.length : 0}
              itemsLength={data ? data.length : 0}
              onSearchKey={(e) => {
                if (e.key === 'Enter') {
                  setSearch(e.target.value.toLowerCase());
                }
              }}
              orderOptions={orderOptions}
              pageSizes={pageSizes}
              toggleModal={() => setAddModalOpen(!addModalOpen)}
            />
            <AddNewModal
              modalOpen={addModalOpen}
              toggleModal={() => setAddModalOpen(!addModalOpen)}
            />

            <EditModal
              modalOpen={editModalOpen}
              toggleModal={() => setEditModalOpen(!editModalOpen)}
              currentUserData={selectedItem}
            />

            <DataTable
              items={data || []}
              selectedItems={selectedItems}
              onCheckItem={onCheckItem}
              currentPage={currentPage}
              totalPage={totalPage}
              onContextMenuClick={onContextMenuClick}
              onContextMenu={onContextMenu}
              onChangePage={setCurrentPage}
            />
          </div>
        </Colxx>
      </Row>
    </>
  );
};

const mapStateToProps = ({ authUser, accountsApp }) => {
  const { accessToken } = authUser;
  const {
    allUsersData,
    currentUserData,
    filters,
    loading,
    message,
    error,
  } = accountsApp;
  return {
    accessToken,
    allUsersData,
    currentUserData,
    filters,
    loading,
    message,
    error,
  };
};

export default connect(mapStateToProps, {
  getUsersAction: getUsers,
  updateFiltersAction: updateFilters,
})(User);
